            <el-menu-item index="/report-name-list-item-action-log"><span>名单操作日志查询</span></el-menu-item>
          </el-submenu>
          <el-menu-item index="/node-monitor"><i class="el-icon-setting"></i><span>节点管理</span></el-menu-item>
          <el-submenu index="4">
            <template #title>
              <i class="el-icon-setting"></i>

              <span>系统管理</span>
            </template>
            <el-menu-item index="/base-config"><span>基础配置</span></el-menu-item>
            <el-menu-item index="/ssl"><span>SSL证书管理</span></el-menu-item>
            <el-menu-item index="/sys-engine-protection"><span>防护引擎管理</span></el-menu-item>
            <el-submenu index="5-5">
              <template #title>防护规则管理</template>
              <el-menu-item index="/web-rule-manage/single_rule">Web防护规则管理</el-menu-item>
              <el-menu-item index="/sys-web-white-rule/single_rule">Web白名单规则管理</el-menu-item>
              <el-menu-item index="/sys-flow-rule-protection/single_rule">流量防护规则管理</el-menu-item>
