    </el-container>
  </div>
  <div v-if="!isInitCheck">
    <el-dialog v-model="dialogInitCheckVisible" title="初始化页面" width="30%" :show-close="false"
      :close-on-click-modal="false" :close-on-press-escape="false">
      <div class="init-check-content">
        <p class="item">
          <span>加载Web防护引擎</span>
          <span class="init-check-status">
            <i class="el-icon-circle-check icon-success" v-if="initCheckStatus == 1"></i>
            <i class="el-icon-circle-close icon-error" v-if="initCheckStatus == 0"></i>
            <i class="el-icon-loading icon-primary" v-if="initCheckStatus == 2"></i>
          </span>
        </p>
        <p class="item">
          <span>加载流量防护引擎</span>
          <span class="init-check-status">
            <i class="el-icon-circle-check icon-success" v-if="initCheckStatus == 1"></i>
            <i class="el-icon-circle-close icon-error" v-if="initCheckStatus == 0"></i>
            <i class="el-icon-loading icon-primary" v-if="initCheckStatus == 2"></i>
          </span>
        </p>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" plain disabled v-if="initCheckStatus == 2">正在初始化，请耐心等待……</el-button>
          <el-button type="primary" @click="dialogInitCheckVisible = false; isInitCheck = true"
            v-if="initCheckStatus == 1">
            已完成初始化，点击请继续
          </el-button>
          <el-button type="primary" v-if="initCheckStatus == 0" @click="sysInit">初始化失败，请重试</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
  <el-backtop :right="50" :bottom="50" />
