<script>

import { JXAjax } from "./assets/scripts/common";
import imgLogo from "./assets/images/logo1800.png";
import { useRouter } from 'vue-router';

export default {
  //mixins: [mixin],
  data() {
    return {
      route: '',
      isCollapse: false,
      activeIndex: "/domain",
      isShowAudit: false,
      isShowYun: false,
      imgLogo: imgLogo,
      isInitCheck: true,
      dialogInitCheckVisible: true,
      initCheckStatus: 2, // 0 失败， 1 成功， 2等待
      //loadingPage: true,
      selectData: [
        {
          value: "1",
          label: "中文",
        },
        {
          value: "2",
          label: "English",
        },
      ],
      selectValue: "中文",
    };
  },

  mounted() {
    var t = this;
    const { currentRoute } = useRouter();
    setTimeout(function () { 
      var url = currentRoute.value.path;
      var path = url.split("/")[1];
      var params = url.split("/")[3];
      t.activeMenu(path, params);
    }, 100)
    var sys_init_check = window.sessionStorage.getItem("sys_init_check")
    if (!sys_init_check) {
      t.initCheck();
    }
  },

  watch: {
    $route(to, from) {
      var path = to.path.split("/")[1]; // web-rule-protection
      var params = to.path.split("/")[3]; // single_rule
      this.activeMenu(path, params);
    }
  },

  methods: {
    activeMenu(path, params) {
      var str = path;
      if (params) {
        str = path + "/" + params;
      }

      switch (str) {
        case "group":
        case "group-protection":
        case "web-engine-protection/group_rule":
        case "web-rule-protection/group_rule":
        case "web-white-rule/group_rule":
        case "web-deny-page/group_rule":
        case "flow-engine-protection/group_rule":
        case "flow-rule-protection/group_rule":
        case "flow-white-protection/group_rule":
        case "flow-deny-page/group_rule":
        case "name-list/group_rule":
          this.activeIndex = "/group"
          break;
        case "web-rule-manage":
        case "web-rule-manage-edit/single_rule":
        case "web-rule-manage-edit/group_rule":
        case "group-rule/web-rule-manage":
          this.activeIndex = "/web-rule-manage/single_rule"
          break;
        case "sys-web-white-rule":
        case "sys-web-white-rule-edit/single_rule":
        case "sys-web-white-rule-edit/group_rule":
        case "group-rule/sys-web-white-rule":
          this.activeIndex = "/sys-web-white-rule/single_rule"
          break;
        case "sys-flow-rule-protection":
        case "sys-flow-rule-protection-edit/single_rule":
        case "sys-flow-rule-protection-edit/group_rule":
        case "group-rule/sys-flow-rule-protection":
          this.activeIndex = "/sys-flow-rule-protection/single_rule"
          break;
        case "sys-flow-white-rule":
        case "sys-flow-white-rule-edit/single_rule":
        case "sys-flow-white-rule-edit/group_rule":
        case "group-rule/sys-flow-white-rule":
          this.activeIndex = "/sys-flow-white-rule/single_rule"
          break;
        case "sys-name-list":
        case "sys-name-list-item":
          this.activeIndex = "/sys-name-list"
          break;
        case "ssl":
          this.activeIndex = "/ssl"
          break;
        case "sys-shared-dict":
          this.activeIndex = "/sys-shared-dict"
          break;
        case "sys-engine-protection":
          this.activeIndex = "/sys-engine-protection"
          break;
        case "sys-component-protection":
          this.activeIndex = "/sys-component-protection"
          break;
        case "report-raw-log":
          this.activeIndex = "/report-raw-log"
          break;
        case "sys-report-conf":
          this.activeIndex = "/sys-report-conf"
          break;
        case "sys-log-conf":
          this.activeIndex = "/sys-log-conf"
          break;
        case "sys-mimetic-defense-conf":
          this.activeIndex = "/sys-mimetic-defense-conf"
          break;
        default:
          this.activeIndex = "/domain"
      }
    },
    onClickFolding() {
      this.isCollapse = !this.isCollapse;
    },
    isChecked() { 
      window.sessionStorage.setItem('sys_init_check', 'true');
      this.isInitCheck = true;
      this.dialogInitCheckVisible = false;
    },
    sysInit() { 
      var t = this;
      t.initCheckStatus = 2;
      JXAjax(
        "post",
        "/sys_init",
        {},
        function (response) {
          if (response.data.result) {
            t.initCheckStatus = 1;
          } else {
            t.initCheckStatus = 0;
          }
        },
        function () {
          t.initCheckStatus = 0;
        },
        "no-message"
      );
    },
    initCheck() { 
      var t = this;
      JXAjax(
        "post",
        "/sys_init_check",
        {},
        function (response) {
          if (response.data.message == "sys_init_fail") {
            t.isInitCheck = false;
            t.dialogInitCheckVisible = true;
            t.initCheckStatus = 2;
            t.sysInit();
          } else { 
            t.isChecked();
          }
        },
        function () {
          
        },
        "no-message"
      );
    }
  },
};
</script>

