import { createRouter, createWebHashHistory } from "vue-router";
const domain = () => import("./components/domain.vue")
const group = () => import("./components/group.vue")
const groupDomain = () => import("./components/group-domain.vue")
const protection = () => import("./components/protection.vue")
const jxcheckEngineProtection = () => import("./components/jxcheck-engine-protection.vue")
const webEngineProtection = () => import("./components/web-engine-protection.vue")
const webRuleProtection = () => import("./components/web-rule-protection.vue")
const webWhiteRule  = () => import("./components/web-white-rule.vue")
const flowRuleProtection = () => import("./components/flow-rule-protection.vue")
const webRuleManage = () => import("./components/web-rule-manage.vue")
const webRuleManageEdit = () => import("./components/web-rule-manage-edit.vue")
const ssl = () => import("./components/ssl.vue")
//const bpmn = () => import("./components/bpmn.vue")
const flowEngineProtection = () => import("./components/flow-engine-protection.vue")
const flowWhiteProtection = () => import("./components/flow-white-protection.vue")
const nameList = () => import("./components/name-list.vue")
const groupProtection = () => import("./components/group-protection.vue")
const groupRule = () => import("./components/group-rule.vue")
const sysWebWhiteRule = () => import("./components/sys-web-white-rule.vue")
const sysWebWhiteRuleEdit = () => import("./components/sys-web-white-rule-edit.vue")
const sysFlowRuleProtection = () => import("./components/sys-flow-rule-protection.vue")
const sysFlowRuleProtectionEdit = () => import("./components/sys-flow-rule-protection-edit.vue")
const sysFlowWhiteRule = () => import("./components/sys-flow-white-rule.vue")
const sysFlowWhiteRuleEdit = () => import("./components/sys-flow-white-rule-edit.vue")
const webDenyPage = () => import("./components/web-deny-page.vue")
const flowDenyPage = () => import("./components/flow-deny-page.vue")
const sysNameList = () => import("./components/sys-name-list.vue")
const sysNameListEdit = () => import("./components/sys-name-list-edit.vue")
const sysNameListItem = () => import("./components/sys-name-list-item.vue")
const sysSharedDict = () => import("./components/sys-shared-dict.vue")
const sysSharedDictEdit = () => import("./components/sys-shared-dict-edit.vue")
const sysEngineProtection = () => import("./components/sys-engine-protection.vue")
const sysComponentProtection = () => import("./components/sys-component-protection.vue")
const globalNameList = () => import("./components/global-name-list.vue")
const globalComponentProtection = () => import("./components/global-component-protection.vue")
const componentProtection = () => import("./components/component-protection.vue")
const baseConfig = () => import("./components/base-config.vue")
const sysAbnormalHandle = () => import("./components/sys-abnormal-handle.vue")
const sysGlobalDefaultPage = () => import("./components/sys-global-default-page.vue")
const nodeMonitor = () => import("./components/node-monitor.vue")
const sysLogConf = () => import("./components/sys-log-conf.vue")
const serviceCenterEngine =() => import("./components/service-center-engine.vue")
const sysReportConf = () => import("./components/sys-report-conf.vue")
const reportWeb = () => import("./components/report-web.vue")
const reportFlow = () => import("./components/report-flow.vue")
const reportNameList = () => import("./components/report-name-list.vue")
const reportNameListItem =() => import("./components/report-name-list-item-action-log.vue")
const reportRawLog = () => import("./components/report-raw-log.vue")
const sysMimeticDefenseConf = () => import("./components/sys-mimetic-defense-conf.vue")


let routes = [
  { path: "/", component: domain },
  { path: "/domain", component: domain },
  { path: "/group", component: group },
  { path: "/group-domain/:groupId", component: groupDomain },
  { path: "/protection/:domain/:ruleType", component: protection },
  { path: "/jxcheck-engine-protection/:domain/:ruleType", component: jxcheckEngineProtection },
  { path: "/web-engine-protection/:domain/:ruleType", component: webEngineProtection },
  { path: "/web-rule-protection/:domain/:ruleType", component: webRuleProtection },
  { path: "/web-white-rule/:domain/:ruleType", component: webWhiteRule },
  { path: "/flow-rule-protection/:domain/:ruleType", component: flowRuleProtection },
  { path: "/ssl", component: ssl },
  { path: "/web-rule-manage/:ruleType", component: webRuleManage },
  { path: "/web-rule-manage-edit/:uuid/:ruleType/:type", component: webRuleManageEdit },
  { path: "/flow-engine-protection/:domain/:ruleType", component: flowEngineProtection },
  { path: "/flow-white-protection/:domain/:ruleType", component: flowWhiteProtection },
  { path: "/name-list/:domain/:ruleType", component: nameList },
  { path: "/group-protection/:groupId", component: groupProtection },
  { path: "/group-rule/:ruleGroupUuid/:backUrl", component: groupRule },
  { path: "/sys-web-white-rule/:ruleType", component: sysWebWhiteRule },
  { path: "/sys-web-white-rule-edit/:uuid/:ruleType/:type", component: sysWebWhiteRuleEdit },
  { path: "/sys-flow-rule-protection/:ruleType", component: sysFlowRuleProtection },
  { path: "/sys-flow-rule-protection-edit/:uuid/:ruleType/:type", component: sysFlowRuleProtectionEdit },
  { path: "/sys-flow-white-rule/:ruleType", component: sysFlowWhiteRule },
  { path: "/sys-flow-white-rule-edit/:uuid/:ruleType/:type", component: sysFlowWhiteRuleEdit },
  { path: "/web-deny-page/:domain/:ruleType", component: webDenyPage },
  { path: "/flow-deny-page/:domain/:ruleType", component: flowDenyPage },
  { path: "/sys-name-list", component: sysNameList },
  { path: "/sys-name-list-edit/:uuid", component: sysNameListEdit },
  { path: "/sys-name-list-item/:uuid", component: sysNameListItem },
  { path: "/sys-shared-dict", component: sysSharedDict },
  { path: "/sys-shared-dict-edit/:uuid", component: sysSharedDictEdit },
  { path: "/sys-engine-protection", component: sysEngineProtection },
  { path: "/sys-component-protection", component: sysComponentProtection },
  { path: "/global-name-list", component: globalNameList },
  { path: "/global-component-protection", component: globalComponentProtection },
  { path: "/component-protection/:domain/:ruleType", component: componentProtection },
  { path: "/base-config", component: baseConfig },
  { path: "/sys-abnormal-handle", component: sysAbnormalHandle },
  { path: "/sys-global-default-page", component: sysGlobalDefaultPage },
  { path: "/node-monitor", component: nodeMonitor },
  { path: "/sys-log-conf", component: sysLogConf }, 
  { path: "/sys-mimetic-defense-conf", component: sysMimeticDefenseConf }, 
  { path: "/service-center-engine", component: serviceCenterEngine },
  { path: "/sys-report-conf", component: sysReportConf },
  { path: "/report-web", component: reportWeb },
  { path: "/report-flow", component: reportFlow },
  { path: "/report-name-list", component: reportNameList },
  { path: "/report-name-list-item-action-log", component: reportNameListItem },
  { path: "/report-raw-log", component: reportRawLog },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes: routes
})

export default router